@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.btn-blanco {
  width: 27rem;
  padding: 0.8rem 3rem;
  border-radius: 4px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  text-transform: uppercase;
  transition: all 0.5s;
}
@media (max-width: 480px) {
  .btn-blanco {
    font-size: 1.2rem;
    padding: 0.8rem 2rem;
    width: 17rem;
  }
}
.btn-blanco:hover {
  background-color: #FFFFFF;
  color: #000000;
}

.btn-negro {
  width: 27rem;
  padding: 0.8rem 3rem;
  border-radius: 4px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #000000;
  color: #000000;
  text-transform: uppercase;
  transition: all 0.5s;
}
@media (max-width: 480px) {
  .btn-negro {
    font-size: 1.2rem;
    padding: 0.8rem 2rem;
    width: 17rem;
  }
}
.btn-negro:hover {
  background-color: #000000;
  color: #FFFFFF;
}

