html {
  font-size: 62.5%;
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  color: #FFFFFF;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background: #000000;
  overflow-x: hidden;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 700;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

section {
  margin-bottom: 5rem;
}

section:last-of-type {
  margin-bottom: 0em;
}

.contenedor-margen {
  margin: auto;
  max-width: 1200px;
}

.main-contacto {
  margin-bottom: 7rem;
}
.main-contacto img {
  width: auto;
  height: auto;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
}
.form .btn-blanco {
  width: 100%;
}

.contenedor-label {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 1rem 2rem 1rem 1rem;
}
.contenedor-label input,
.contenedor-label textarea,
.contenedor-label select {
  width: 100%;
  padding: 0.5rem;
  border-radius: 4px;
  background-color: dimgray;
}
.contenedor-label input::placeholder,
.contenedor-label textarea::placeholder,
.contenedor-label select::placeholder {
  color: #FFFFFF;
}
.contenedor-label textarea {
  max-height: 4rem;
}

.label {
  width: 15rem;
  text-align: end;
}

.main-new {
  margin-bottom: 7rem;
}
.main-new .new-contenedor-img {
  height: 95rem;
}
.main-new img {
  object-fit: cover;
  object-position: center center;
  height: 95rem;
}

.new-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.new__title {
  font-weight: 900;
  text-align: left;
  margin: 0;
}

.new__paragraph {
  line-height: 33px;
}

.header {
  background-color: #000000;
  padding: 0 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 1rem;
  position: relative;
}

.header-link {
  z-index: 101;
}

.header__title {
  color: #FFFFFF;
  font-weight: 800;
}

.list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 3rem;
  display: none;
}
@media (min-width: 768px) {
  .list {
    display: flex;
  }
}

.list__item a {
  color: #FFFFFF;
  transition: opacity 0.3s;
}
.list__item a:hover {
  opacity: 0.7;
}

.container-menu {
  position: absolute;
  top: -200px;
  transition: all 1s;
}

.activecontainer-menu {
  display: block;
  position: absolute;
  background: rgb(61, 61, 61);
  width: 100%;
  padding: 3rem;
  top: 118.4px;
  z-index: 99;
}

.list-responsive {
  display: flex;
  flex-direction: column;
}

/* Menu Hambuerguesa */
.bars__menu {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 0;
  z-index: 10;
}
.bars__menu span {
  display: block;
  height: 0.3rem;
  width: 100%;
  border-radius: 0.1rem;
  background-color: #FFFFFF;
  transform-origin: 0 100%;
  transition: all 0.3s;
}
.bars__menu .activeline1__bars-menu {
  transform: rotate(45deg) translate(-2px, 1px);
}
.bars__menu .activeline2__bars-menu {
  opacity: 0;
  margin-left: -30px;
}
.bars__menu .activeline3__bars-menu {
  transform: rotate(-45deg) translate(-4px, 2px);
}
@media (min-width: 768px) {
  .bars__menu {
    display: none;
  }
}

.cover {
  position: relative;
  width: 100%;
  max-height: auto;
}

.cover__content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
}

.cover__content__title {
  font-weight: 900;
  font-size: 3rem;
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .cover__content__title {
    font-size: 4.8rem;
  }
}

.cover__content__paragraph {
  font-weight: 700;
  font-size: 1.6rem;
  max-width: 90rem;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .cover__content__paragraph {
    font-size: 2.4rem;
  }
}

.cover__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.news {
  position: relative;
}

.container-triangle {
  position: absolute;
  max-width: 1200px;
  height: auto;
  background-color: orange;
}

.triangle {
  z-index: -1;
}

