.new-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3rem;
  z-index: 100;
}

.news__title {
  font-weight: 700;
  color: #ff1b1b;
  margin: 0;
  z-index: 1000;
}

.card {
  max-width: 40rem;
  border-radius: 4px;
  overflow: hidden;
  padding: 2rem;
  box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -webkit-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -moz-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  transition: 0.4s;
  z-index: 100;
  border-top: 2px solid rgba(255, 255, 255, 0.3);
  border-left: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
}
.card .card__content__title {
  color: #FFFFFF;
}
.card .card__content__paragraph {
  color: #FFFFFF;
}
.card:hover {
  background: #ff1b1b;
  transform: translateY(-10px);
}
.card:hover .card-img {
  width: 110%;
}

.card__content-img {
  width: 100%;
  border-radius: 4px;
  overflow: hidden;
}

.card-img {
  width: 100%;
  object-fit: cover;
  object-position: center;
  transition: 0.4s;
}

.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.card__content__title {
  font-weight: 400;
  margin: 0;
}

.card__content__paragraph {
  text-align: justify;
}

.modelos {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3rem;
}

.modelos__title {
  font-weight: 900;
}

.modelo {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
  width: 100%;
}
@media (min-width: 768px) {
  .modelo {
    flex-direction: row;
    gap: 0;
  }
}

.modelo:nth-child(odd) {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
}
@media (min-width: 768px) {
  .modelo:nth-child(odd) {
    flex-direction: row;
    gap: 0;
  }
}

.modelo__img {
  width: 100%;
}
@media (min-width: 768px) {
  .modelo__img {
    width: 50%;
  }
}

.modelo__img img {
  width: 100%;
  height: 70rem;
  object-fit: cover;
  object-position: center;
}

.modelo-description {
  width: 100%;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}
@media (min-width: 768px) {
  .modelo-description {
    width: 50%;
  }
}

.modelo-description__title {
  font-weight: 700;
  text-align: left;
}

.modelo-description__paragraph {
  font-weight: 400;
  max-width: 50rem;
  text-align: justify;
}

.modelo-description__price {
  font-weight: 700;
  max-width: 50rem;
}

.contacto {
  position: relative;
  width: 100%;
  height: 30rem;
}

.contacto__content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.contacto__content__title {
  color: #FFFFFF;
  font-weight: 900;
  margin: 0;
}

.contacto__content__paragraph {
  color: #FFFFFF;
}

.content__video {
  position: absolute;
  width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center center;
  z-index: -1;
}

.footer {
  background-color: #000000;
  z-index: 100;
}
.footer ul {
  justify-content: space-evenly;
}
.footer ul a {
  transition: opacity 0.3s;
}
.footer ul a:hover {
  opacity: 0.7;
}
.footer a {
  color: #FFFFFF;
}

.copyrigth {
  color: #FFFFFF;
  text-align: center;
}